import { mapState, mapMutations } from 'vuex';
import { login } from '@/api/login.js'
export default {
	data() {
		return {
			hasProvider: false, // 是否有服务商平台可登录
			providerList: [],
			account: '',
			password: ''
		};
	},
	onReady() {
		this.getProvider();
	},
	onLoad: function() {

	},

	methods: {
		...mapMutations(['setLogin']),
		// 微信授权获取信息 获取 wx.getUserInfo 接口后续将不再出现授权弹窗，请注意升级
		getUserInfo(res) {
			if (!res.detail.iv) {
				uni.showToast({
					icon: "none",
					title: "您取消了授权,登录失败"
				});
				return false;
			}
			// 用户授权，获取用户基本信息和加密数据（encryptedData、iv），后台进行查询配对用户，生成userInfo返回前台
			// 这里有一个验证用户接口
			setTimeout(() => {
				let profile = JSON.parse(res.detail.rawData)
				profile.nickname = profile.nickName

				const temp = {
					token: '',
					profile,
				}
				this.setLogin(temp)
				uni.switchTab({
					url: '/pages/index/index'
				});
			}, 100);
		},
		getProvider() {
			const providerList = ['weixin', 'qq', 'sinaweibo'];
			// 获取服务供应商。判断是否用某种登录工具登录
			uni.getProvider({
				service: 'oauth',
				success: res => {
					if (res.provider && res.provider.length) {
						for (let i = 0; i < res.provider.length; i++) {
							var tempProvider = res.provider[i];
							if (~providerList.indexOf(tempProvider)) {
								this.providerList.push({
									value: tempProvider,
									image: '/static/login/' + tempProvider + '.png'
								});
							}
						}
						this.hasProvider = true;
					}
				},
				fail: err => {
					console.error('获取服务供应商失败：' + JSON.stringify(err));
				}
			});
		},
		bindLogin() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			const account = this.account.trim()
			const password = this.password.trim()
			if (account == '') {
				this.alert('请输入手机号')
				return;
			}
			if (!(/^1[3456789]\d{9}$/.test(account))) {
				this.alert("手机号码有误，请重填");
				return false;
			}
			if (password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}

			uni.showLoading({
				title: '登录中'
			});

			login({ phone: this.account, password: this.password }).then(res => {
				const temp = {
					token: JSON.parse(res.bindings[1].tokenJsonStr).access_token,
					profile: res.profile
				}
				this.setLogin(temp)
				
				uni.switchTab({
					url: '/pages/index/index'
				});
			}).catch(err => {
				this.$u.toast(err.msg || '请稍后重试')
			})
		},
		authLogin(value) {
			console.log(value)
			uni.login({
				provider: value,
				success: res => {
					console.log(res)
					uni.getUserInfo({
						provider: value,
						success: infoRes => {
							console.log(infoRes)
							// 存储个人信息
							// 这里有一个验证用户接口

							let profile = null
							if (value == 'weixin') {
								infoRes.userInfo.nickname = infoRes.userInfo.nickName
								profile = infoRes.userInfo
							}

							const temp = {
								token: '',
								profile,
							}
							this.storeLogin(temp)

							// 登录完之后到首页
							uni.switchTab({
								url: '/pages/index/index'
							});
						}
					});
				},
				fail: err => {
					console.error('授权失败：' + JSON.stringify(err));
				}
			});
		},
		// 验证报错
		alert(msg) {
			uni.showToast({
				icon: 'none',
				title: msg
			});
		}
	}
};
